
<template>
    <v-container>
        <v-text-field autofocus label="Электронный адрес" v-model="UserName" />
        <v-text-field label="Пароль" v-model="Password" />
        <v-checkbox v-model="RememberMe" dense label="Запомнить меня"></v-checkbox>
        <v-btn block color="primary" elevation="4" outlined :loading="Loading" @click="SignInAsync" rounded large
            text>Войти</v-btn>
    </v-container>
</template>

<script lang="ts">
/* eslint-disable */
import IDBaseComponent from "./base/id-base-component";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";

@Component
export default class LoginComponent extends IDBaseComponent {
    @Prop({ required: false, default: '' }) returnUrl?: any;

    UserName = '';
    Password = '';
    PasswordVisible = false;
    RememberMe = false;

    async SignInAsync(): Promise<void> {

        let returnParam = '';

        for(let param in this.$route.query){
            returnParam += `&${param}=${this.$route.query[param]}`;
        }

        returnParam = `?${returnParam.substring(1, returnParam.length)}`;
        await fetch(`https://localhost:44338/api/account/login`, {
            method: "POST",
            headers: {
                'Accept': 'text/plain',
                'Content-Type': 'text/json;+charset=utf-8'
            },
            body: JSON.stringify({ UserName: this.UserName, Password: this.Password, RememberMe: this.RememberMe, ReturnUrl: returnParam })
        });
    }
}
</script>